import React from 'react'
import './index.css'
export default function Register() {
    const nameRef=React.useRef()
    const passwordRef=React.useRef()
    const rePasswordRef=React.useRef()
    return (
        <div className='user-register'>
            <div className='user-title'>注册您的账号</div>
        <div className='name-input'>用户名：<input ref={nameRef} type='text' className='user-input'></input></div>
           <div className='password-input'>密码：<input ref={passwordRef} type='password' className='user-input'></input></div>
           <div className='re-Password-input'>重复密码：<input ref={rePasswordRef} type='password' className='user-input'></input></div>
        </div>
    )
}
